import React from "react";
// import { zoom } from "@nishanths/zoom.js";
import cx from "classnames";

class ZoomImage extends React.Component {
  constructor(props) {
    super(props);

    this.image = React.createRef();
  }

  componentDidMount = () => {
    zoom.setup(this.image.current);
  }

  render() {
      return <img ref={this.image} {...this.props} />
  }
}

export const ImageGrid = ({ images, className, ...props }) => {
  const classNames = cx("images", className);

  return (
    <div className={classNames} {...props}>
      {images.map((image, i) => {
        const imgSrc = typeof image === "string" ? image : image.src;
        return (
          <div className="screenshot" key={i}>
            <ZoomImage src={imgSrc} className="img-fluid rounded" />
          </div>
        );
      })}
    </div>
  );
};
